<template>
    <div class="videoPlayer">
        <div class="header">
            <span @click="back" class="back">
                <img src="../../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="usersTitle"></span>
            <span class="back"></span>
            <span class="upload" @click="share"></span>
        </div>
        <div class="content">
            <div class="player">
              <video-player class="vjs-custom-skin"
                            ref="videoPlayer"
                            :options="playerOptions"
                            :playsinline="true"
                            @play="onPlayerPlay($event)"
                            @pause="onPlayerPause($event)"
                            @ended="onPlayerEnded($event)"
                            @loadeddata="onPlayerLoadeddata($event)"
                            @waiting="onPlayerWaiting($event)"
                            @playing="onPlayerPlaying($event)"
                            @timeupdate="onPlayerTimeupdate($event)"
                            @canplay="onPlayerCanplay($event)"
                            @canplaythrough="onPlayerCanplaythrough($event)"
                            @ready="playerReadied"
                            @statechanged="playerStateChanged($event)">
              </video-player>
            </div>
        </div>
        <div class="videoInfo">
          <div class="videoTitle">{{ videoInfo.fVideoName }}</div>
          <div class="playCounts">11</div>
          <div class="videoDetails">
            
            <br>
            <span>{{ videoInfo.fVideoNote }}</span>
          </div>
        </div>
    </div>
</template>

<script>
import store from '../../vuex/store'
import '../../assets/css/custom-theme.css'
import { videoPlayer } from 'vue-video-player'
export default {
  name: 'videoPlay',
  data () {
    return {
      videoInfo: {},
      playerOptions: {
        muted: false,
        language: 'zh-CN',
        playbackRates: [0.7, 1.0, 1.5, 2.0],
        sources: {
          type: 'video/mp4',
          src: store.state.videoInfo.fVideoUrl
        },
        poster: store.state.videoInfo.fVideoCoverImgUrl,
        fluid: true
      }
    }
  },
  mounted () {
    this.videoInfo = store.state.videoInfo
    // console.log(this.videoInfo)
  },
  computed: {
    player () {
      return this.$refs.videoPlayer.player
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    share () {
      console.log('share')
    },
    onPlayerPlay (player) {
      // console.log('player play!', player)
    },
    onPlayerPause (player) {
      // console.log('player pause!', player)
      // document.querySelector('video').pause()
    },
    onPlayerEnded (player) {
      // console.log('player ended!', player)
    },
    onPlayerLoadeddata (player) {
      // console.log('player Loadeddata!', player)
    },
    onPlayerWaiting (player) {
      // console.log('player Waiting!', player)
    },
    onPlayerPlaying (player) {
      // console.log('player Playing!', player)
    },
    onPlayerTimeupdate (player) {
      // console.log('player Timeupdate!', player.currentTime())
    },
    onPlayerCanplay (player) {
      // console.log('player Canplay!', player)
    },
    onPlayerCanplaythrough (player) {
      // console.log('player Canplaythrough!', player)
    },
    // or listen state event
    playerStateChanged (playerCurrentState) {
      // console.log('player current update state', playerCurrentState)
    },
    // player is ready
    playerReadied (player) {
      // seek to 10s
      // console.log('example player 1 readied', player)
      // player.currentTime(10)
      // console.log('example 01: the player is readied', player)
    }
  },
  components: {
    videoPlayer
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    color: #333333;
    font-size: 0.48rem;
  }

  .header {
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: relative;
    font-size: 0.48rem;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  img {
    width: 100%;
  }

  .upload {
    position: absolute;
    right: 0;
  }

  .content {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
  }

  .player {
    width: 100%;
    margin: 0 auto;
    height: 5.2rem;
  }

  .videoInfo {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
    margin-top: 0.37333rem;
  }

  .videoTitle,
  .playCounts,
  .videoDetails {
    text-align: start;
    color: #333333;
  }

  .videoDetails {
    margin-top: 0.37333rem;
  }

  .playCounts,
  .videoDetails span {
    color: #666666;
    font-size: 0.4rem;
  }
</style>



